const setContact = (e, type, key, blockKey, state, setter) => {
  const updatedState = state.map(item => {
    if (item.key === blockKey) {
      item?.fields.map(field => {
        if (field.key === key) {
          field.value = e.target.value
        }
      })
    }
    return item
  })
  setter(updatedState)
}

export default setContact