import React, { useContext } from 'react'
import { StateContext } from '../State'

const AddField = ({ blockKey }) => {
  const [state, setState] = useContext(StateContext)
  const fields = [
    {
      type: 'phone',
      label: 'Телефон',
      value: '',
      relatives: ['phone']
    },
    {
      type: 'email',
      label: 'Email',
      value: ''
    },
    {
      type: 'adress',
      label: 'Адрес',
      value: ''
    },
    {
      type: 'comment',
      label: 'Комментарий',
      value: ''
    },
    {
      type: 'fb',
      label: 'Facebook',
      value: ''
    },
    {
      type: 'instagram',
      label: 'Instagram',
      value: ''
    },
    {
      type: 'vk',
      label: 'VKontakte',
      value: ''
    }
  ]

  const addSelectedField = (e, blockKey) => {
    if (!e || !blockKey) {
      return
    }
    const updatedState = state.map(item => {
      if (item.key === blockKey) {
        item.fields = [...item.fields, fields?.filter(field => {
          if (field.type === e.target.value) {
            field.key = `${field.type}_${new Date().getTime()}`
            field.blockKey = blockKey
            return field
          }
        })[0]]
      }
      return item
    })
    setState(updatedState)
  }

  return(
    <div className='add-field'>
      <button className='add'>Добавить поле</button>
      <select className='add-select' defaultValue={false} value={false} onChange={(e) => {addSelectedField(e, blockKey)}}>
        <option defaultChecked={true}></option>
        {
          fields.map(field => {
            return <option value={field?.type}>{field?.label}</option>
          })
        }
      </select>
    </div>
  )
}

export default AddField