import React, { useContext } from 'react'
import { StateContext } from '../State'
import phoneRelativeLabels from '../constants/phoneRelativeLabels'
import setPhoneRelatives from '../handlers/setPhoneRelatives'

const PhoneRelative = ({ props }) => {
  const [state, setState] = useContext(StateContext)
  const {objKey, blockKey, key, checked} = props
  return(
    <label className='checkbox'>
      <input type='checkbox'
             name={objKey}
             checked={checked}
             className='checkbox-input'
             onChange={(e) => {setPhoneRelatives(e, blockKey, key, state, setState)}}
      />
      <div className='checkbox-shape'></div>
      <span className='checkbox-label'>{phoneRelativeLabels[objKey]}</span>
    </label>
  )
}

export default PhoneRelative