import React, { useContext } from 'react'
import { StateContext } from '../State'

const PositionContacts = ({blockKey, index}) => {
  const [state, setState] = useContext(StateContext)

  const moveUp = () => {
    const updatedState = state.map(item => {
      if (item.key === blockKey) {
        item.fields = item.fields.map((field, i) => {
          if (i === index-1) {
            return item.fields[index]
          }
          if (i === index) {
            return item.fields[index-1]
          }
          return field
        })
      }
      return item
    })
    setState(updatedState)
  }

  const moveDown = () => {
    const updatedState = state.map(item => {
      if (item.key === blockKey) {
        item.fields = item.fields.map((field, i) => {
          if (i === index) {
            return item.fields[index+1]
          }
          if (i === index+1) {
            return item.fields[index]
          }
          return field
        })
      }
      return item
    })
    setState(updatedState)
  }

  const removeBlock = () => {
    const updatedState = state.map(item => {
      if (item.key === blockKey) {
        item.fields = item.fields.filter((field, i) => i !== index)
      }
      return item
    })
    setState(updatedState)
  }

  const contactFields = state.filter(item => item.key === blockKey)[0].fields
  const moveUpDisabled = index ? false : true
  const moveDownDisabled = contactFields.length - 1 === index ? true : false

  return(
    <div className='control-actions'>
      <button className='action action-remove' type='button' onClick={removeBlock}></button>
      <button className='action action-up' type='button' onClick={moveUp} hidden={moveUpDisabled}></button>
      <button className='action action-down' type='button' onClick={moveDown} hidden={moveDownDisabled}></button>
    </div>
  )
}

export default PositionContacts