import React, { useContext } from 'react'
import { StateContext } from './State'
import phoneRelativeLabels from './constants/phoneRelativeLabels'

const ReceiptView = () => {
  const [state] = useContext(StateContext)
  return(
    <div key='check-view' className='check-view'>

      <div className='check-letter'>

        {
          state?.map(item => {
            switch (item.type) {
              case 'logo':
                return <CheckLogo itemProps={item} />
              case 'products':
                return <CheckProductsList />
              case 'message':
                return <CheckMessage itemProps={item} />
              case 'contacts':
                return <CheckContacts itemProps={item} />
              case 'site':
                return <CheckSite itemProps={item} />
            }
          })
        }

      </div>

    </div>
  )
}

const CheckDivider = () => {
  const [state] = useContext(StateContext)
  const productsSection = state.filter(item => item.type === 'products')[0]
  const isVisible = productsSection && productsSection.blocksDividers
  return(
    isVisible
      ? <div className='check-divider'></div>
      : ''
  )
}

const CheckProductsDivider = () => {
  const [state] = useContext(StateContext)
  return(
    state.filter(item => item.type === 'products')[0].selfDividers
      ? <div className='check-divider'></div>
      : ''
  )
}

const CheckLogo = ({itemProps}) => {
  const { visible, image } = itemProps
  return(
    visible
      ? <>
          <div className='check-logo'>
            <img className='check-logo-img' src={image} alt=''/>
          </div>
          <CheckDivider />
        </>
      : ''
  )
}

const CheckQR = () => {
  return(
    <div className='check-entry entry-qr'>
      <div className='entry-qr'>
        <img className='entry-qr-img' src='assets/qr-prod.svg' alt=''/>
      </div>
    </div>
  )
}

const CheckMessage = ({itemProps}) => {
  const { visible, message, title } = itemProps

  return(
    visible
      ? <>
        <div className='check-entry'>
          <div className='entry-title'>{title}</div>
          <div>{message}</div>
        </div>
        <CheckDivider />
      </>
      : ''
  )
}

const CheckContacts = ({itemProps}) => {
  const { visible, fields, title, key } = itemProps

  return(
    visible
      ?
      <>
        <div key={key} className='check-entry entry-contacts'>
          <div className='entry-title'>{title}</div>
          { fields?.map(field => {
            const label = field.relatives && field.relatives.length
              ? field.relatives
                .map(item => phoneRelativeLabels[item])
                .join(', ')
              : field.label
            return <CheckContactsField label={label} value={field?.value} />
          }) }
        </div>
        <CheckDivider />
      </>
      : ''
  )
}

const CheckContactsField = ({label, value}) => {
  return(
    <div className='entry-contact'>
      <div className='entry-contact-label'>{label}</div>
      <div className='entry-contact-value'>{value}</div>
    </div>
  )
}

const CheckSite = ({itemProps}) => {
  const { visible, url, title } = itemProps

  return(
    visible
      ? <>
        <div className='check-entry entry-site'>
          <div className='entry-title'>{title}</div>
          <div className='entry-site-qr'>
            <img className='entry-site-qr-img' src='assets/qr-site.svg' alt=''/>
            <div className='entry-site-url'>{url}</div>
          </div>
        </div>
        <CheckDivider />
      </>
      : ''
  )
}

const CheckProducts = ({label, value}) => {
  return(
    <div className='check-entry entry-columns'>
      <div className='entry-label'>{label}</div>
      <div className='entry-right'>{value}</div>
    </div>
  )
}

const CheckProductsList = () => {
  return(
    <>
      <CheckProducts label='Товар 1' value='150 ₽' />
      <CheckProductsDivider />
      <CheckProducts label='Товар 2' value='250 ₽' />
      <CheckProductsDivider />
      <CheckProducts label='Товар 3' value='1200 ₽' />
      <CheckProductsDivider />
      <CheckProducts label='ИТОГО' value='1550 ₽' />
      <CheckProductsDivider />
      <CheckQR />
      <CheckDivider />
    </>
  )
}

export default ReceiptView