import React, { useContext } from 'react'
import { StateContext } from './State'
import EditLogo from "./components/EditLogo";
import EditContacts from "./components/EditContacts";
import EditMessage from "./components/EditMessage";
import EditProducts from "./components/EditProducts";
import EditSite from "./components/EditSite";
import AddBlock from "./components/AddBlock";

const ReceiptManagement = () => {
  const [state] = useContext(StateContext)

  return(
    <div key='check-control' className='check-control'>

      <h1 className='check-control-caption'>БЛОКИ И ИХ ПОРЯДОК</h1>

      {
        state?.map((item, index) => {
          switch (item.type) {
            case 'logo':
              return <EditLogo itemProps={item} index={index} />
            case 'products':
              return <EditProducts itemProps={item} index={index} />
            case 'message':
              return <EditMessage itemProps={item} index={index} />
            case 'contacts':
              return <EditContacts itemProps={item} index={index} />
            case 'site':
              return <EditSite itemProps={item} index={index} />
          }
        })
      }

      <AddBlock />

    </div>
  )
}

export default ReceiptManagement