const setPhoneRelatives = (e, blockKey, key, state, setter) => {
  const updatedState = state.map(item => {
    if (item.key === blockKey) {
      item.fields.map(field => {
        if (field.key === key) {
          field.relatives.includes(e.target.name)
            ? field.relatives = field.relatives.filter(item => item !== e.target.name)
            : field.relatives.push(e.target.name)
        }
      })
    }
    return item
  })
  setter(updatedState)
}

export default setPhoneRelatives