import React, { useContext } from 'react'
import { StateContext } from '../State'

const Position = ({removeDisabled, index}) => {
  const [state, setState] = useContext(StateContext)

  const moveUp = () => {
    const updatedState = state.map((item, i) => {
      if (i === index-1) {
        return state[index]
      }
      if (i === index) {
        return state[index-1]
      }
      return item
    })
    setState(updatedState)
  }

  const moveDown = () => {
    const updatedState = state.map((item, i) => {
      if (i === index) {
        return state[index+1]
      }
      if (i === index+1) {
        return state[index]
      }
      return item
    })
    setState(updatedState)
  }

  const removeBlock = () => {
    const updatedState = state.filter((item, i) => i !== index)
    setState(updatedState)
  }

  const moveUpDisabled = index ? false : true
  const moveDownDisabled = state.length - 1 === index ? true : false

  return(
    <div className='control-actions'>
      {
        removeDisabled ? null : <button className='action action-remove' onClick={removeBlock}></button>
      }
      <button className='action action-up' onClick={moveUp} hidden={moveUpDisabled}></button>
      <button className='action action-down' onClick={moveDown} hidden={moveDownDisabled}></button>
    </div>
  )
}

export default Position