import React, { useContext } from 'react'
import { StateContext } from '../State'
import Position from './Position'
import ContactsField from './ContactsField'
import AddField from "./AddField";
import setVisibility from '../handlers/setVisibility'
import setValue from '../handlers/setValue';

const EditContacts = ({itemProps, index}) => {
  const [state, setState] = useContext(StateContext)
  const { type, key, title, fields, displayName } = itemProps

  return(
    <div className='control-item'>

      <Position index={index} />

      <div className='control-item-title'>
        <div className='item-trigger'>
          <label className='trigger'>
            <input type='checkbox'
                   defaultChecked={true}
                   onChange={(e) => {setVisibility(e, type, key, state, setState)}}
                   className='trigger-input'/>
            <div className='trigger-shape'></div>
            <span className='trigger-label'>{displayName}</span>
          </label>
        </div>
      </div>

      <label className='field'>
        <div className='field-label'>Подзаголовок секции (опционально)</div>
        <input className='field-input' name='title' type='text' value={title} onChange={(e) => {setValue(e, type, key, state, setState)}} />
      </label>

      {
        fields.map((field, index) => {
          return <ContactsField fieldProps={field} index={index}/>
        })
      }

      <AddField blockKey={key} />

    </div>
  )
}

export default EditContacts