import React, { useContext, useRef, useState, useEffect } from 'react'
import { StateContext } from '../State'
import Position from './Position'
import setVisibility from '../handlers/setVisibility';

const EditLogo = ({itemProps, index}) => {
  const [state, setState] = useContext(StateContext)
  const [pic, setPic] = useState(null)
  const [preview, setPreview] = useState(null)
  const inputFileRef = useRef()
  const { type, key, image, displayName } = itemProps

  useEffect(() => {
    if (pic) {
      const reader = new FileReader()
      reader.onloadend = () => {
        setPreview(reader.result)
      }
      reader.readAsDataURL(pic)
    }
  }, [pic])

  useEffect(() => {
    if (preview) {
      const updatedState = state.map((item, i) => {
        if (item.key === key) {
          item.image = preview
        }
        return item
      })
      setState(updatedState)
    }
  }, [preview])

  return(
    <div className='control-item'>

      <Position index={index} />

      <div className='control-item-title'>
        <div className='item-trigger'>
          <label className='trigger'>
            <input type='checkbox'
                   defaultChecked={true}
                   onChange={(e) => {setVisibility(e, type, key, state, setState)}}
                   className='trigger-input'/>
            <div className='trigger-shape'></div>
            <span className='trigger-label'>{displayName}</span>
          </label>
        </div>
      </div>

      <div className='control-logo'>
        <img className='control-logo-img' src={image} alt=''/>
        <form>
          <input type='file'
                 name='image'
                 ref={inputFileRef}
                 style={{ display: 'none' } }
                 accept='image/*'
                 onChange={(event) => {
                   const file = event.target.files[0]
                   if (file && file.type.substr(0, 5) === 'image') {
                     setPic(file)
                   }
                 }}
          />
          <button className='button' onClick={(event) => {
            event.preventDefault()
            inputFileRef.current.click()
          }}>Изменить</button>
        </form>
      </div>
    </div>
  )
}

export default EditLogo