import React, { useContext } from 'react'
import { StateContext } from '../State'
import PositionContacts from './PositionContacts'
import PhoneRelative from './PhoneRelative'
import phoneRelativeLabels from '../constants/phoneRelativeLabels';
import setContact from '../handlers/setContact';

const ContactsField = ({fieldProps, index}) => {
  const [state, setState] = useContext(StateContext)
  const { type, key, blockKey, label, value, relatives } = fieldProps
  return(
    <>
      <div className='field'>
        <PositionContacts index={index} blockKey={blockKey} />
        <div className='field-label'>{label}</div>
        <input className='field-input'
               name={type}
               type='text'
               value={value}
               onChange={(e) => {setContact(e, type, key, blockKey, state, setState)}}></input>
      </div>
      {
        type === 'phone'
          ? <div className='phone-relations'>
            {
              Object.keys(phoneRelativeLabels).map(objKey => {
                return <PhoneRelative props={{objKey, blockKey, key, checked: relatives.includes(objKey)}} />
              })
            }
          </div>
          : ''
      }
    </>
  )
}

export default ContactsField